// Use REAL Zustand stores - TESTING PHILOSOPHY APPROACH
// No mocking - test actual store behavior and real PWA functionality


import React from 'react';
import { render, screen } from '@testing-library/react';
import { BrowserRouter } from 'react-router-dom';
import PWAFeatures from '@/features/pwa/components/PWAFeatures';
import { T } from '@/lib/testing/testIds';

describe('PWA Features - Real Zustand Testing', () => {
  beforeEach(() => {
    // Reset Zustand stores to initial state
    const { usePWAStore } = require('@/lib/stores');
    const store = usePWAStore.getState();
    
    // Reset to initial state
    store.setInstallation({
      isInstalled: false,
      installPrompt: null,
      canInstall: true,
      installSource: 'manual',
      version: '1.0.0',
    });
    
    store.setOffline({
      isOnline: true,
      isOffline: false,
      lastOnline: new Date().toISOString(),
      offlineData: {
        cachedPages: [],
        cachedResources: [],
        queuedActions: [],
      },
    });
    
    store.setNotifications([]);
    store.setLoading(false);
    store.setError(null);
  });

  describe('Component Rendering', () => {
    it('should render PWA features when installation is available', () => {
      render(
        <BrowserRouter>
          <PWAFeatures />
        </BrowserRouter>
      );

      // Check for basic PWA elements
      expect(screen.getByTestId(T.pwa.status)).toBeInTheDocument();
      expect(screen.getByTestId(T.pwa.installButton)).toBeInTheDocument();
    });

    it('should render with custom className', () => {
      render(
        <BrowserRouter>
          <PWAFeatures className="custom-class" />
        </BrowserRouter>
      );

      const container = screen.getByTestId(T.pwa.status);
      expect(container).toHaveClass('custom-class');
    });
  });

  describe('Offline Features', () => {
    it('should display offline indicator when offline', () => {
      // Use REAL Zustand store to set offline state
      const { usePWAStore } = require('@/lib/stores');
      const store = usePWAStore.getState();
      
      // Set offline state using real store
      store.setOffline({
        isOnline: false,
        isOffline: true,
        lastOnline: new Date().toISOString(),
        offlineData: {
          cachedPages: [],
          cachedResources: [],
          queuedActions: [],
        },
      });

      render(
        <BrowserRouter>
          <PWAFeatures />
        </BrowserRouter>
      );

      expect(screen.getByTestId(T.pwa.offlineIndicator)).toBeInTheDocument();
    });
  });

  describe('Performance', () => {
    it('should render within performance budget', async () => {
      const startTime = performance.now();
      
      render(
        <BrowserRouter>
          <PWAFeatures />
        </BrowserRouter>
      );
      
      const endTime = performance.now();
      const renderTime = endTime - startTime;
      
      // Should render within 100ms
      expect(renderTime).toBeLessThan(100);
    });

    it('should not cause memory leaks', () => {
      const { unmount } = render(
        <BrowserRouter>
          <PWAFeatures />
        </BrowserRouter>
      );

      // Unmount component
      unmount();

      // Should not leave any event listeners or timers
      expect(jest.getTimerCount()).toBe(0);
    });
  });
});
